<template>
  <div id="map-container">
    <div class="gray-layer"></div>

    <yandex-map
      ref="yMap"
      zoom="13"
      :coords="mapOption.coords"
      :controls="mapOption.controls"
      style="width: 100%; height: 100%;"
      @map-was-initialized="initNew"
    >
      <ymap-marker
        :coords="mapOption.coords"
        marker-type="circle"
        circle-radius="100"
        marker-id="0"
      />
      <ymap-marker
        v-for="(marker, index) in markers"
        :key="index + 1"
        :marker-id="index + 1"
        :marker-fill="fill"
        :marker-stroke="stroke"
        :coords="marker.coords"
        :hint-content="marker.hintContent"
        marker-type="circle"
        circle-radius="75"
      />
    </yandex-map>
  </div>
</template>

<script>
export default {
  name: 'TheMap',
  data: () => ({
    fill: {
      color: '#000000',
      opacity: 0.4,
    },
    stroke: {
      color: '#ff0000',
      width: 1,
    },
  }),
  computed: {
    mapOption() {
      return this.$store.getters.getMapOption;
    },
    markers() {
      return this.$store.getters.getMapMarkers;
    },
  },
  methods: {
    initNew() {
      const multiRoute = new ymaps.multiRouter.MultiRoute({
        referencePoints: [
            [55.734876, 37.59308],
            'Москва, ул. Мясницкая'
        ],
        params: {
          results: 1,
        }
      }, {
          boundsAutoApply: true
      });

      const referencePoints = multiRoute.model.getReferencePoints();
      referencePoints.splice(1, 0, [55.7955866, 37.546643]);
      referencePoints.splice(2, 0, [55.7944876, 37.536643]);
      referencePoints.splice(3, 0, "Москва, ул. Солянка, 11");
      multiRoute.model.setReferencePoints(referencePoints, [1]);

      this.$refs.yMap.myMap.geoObjects.add(multiRoute);
    },
  }
};
</script>

<style scoped>
#map-container {
  width: 70%;
  height: 100vh;
}
#map-container > div {
  width: 100%;
  height: 100%;
}

.gray-layer {
  position: absolute;
}
</style>

